/* Basicas */
@font-face {
  font-family: 'Anurati';
  font-style: normal;
  font-weight: 400;
  src: local("Anurati"), url("https://fonts.cdnfonts.com/s/14024/Anurati-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Days';
  font-style: normal;
  font-weight: 400;
  src: local("Days"), url("https://fonts.cdnfonts.com/s/104/Days.woff") format("woff"); }

@font-face {
  font-family: 'Mido';
  font-style: normal;
  font-weight: 500;
  src: local("Mido"), url("https://fonts.cdnfonts.com/s/303/Mido.woff") format("woff"); }

html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #0F0F0F;
  overflow: unset;
  padding: 0;
  font-family: Days, sans-serif;
  user-select: none; }

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto; }

.contenedorMayor {
  width: 98%;
  max-width: 140rem;
  margin: 0 auto; }

h1, h2, h3 {
  margin: 0;
  font-weight: 300;
  text-align: center;
  color: #000000;
  font-family: Anurati, sans-serif; }

ul {
  list-style: none;
  padding-left: 0;
  margin: 0; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

p {
  font-size: 2rem; }

a {
  text-decoration: none; }

