/** Media Queries **/
.header {
  background-image: url("/build/img/Imagen_codigo.webp");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: auto;
  background-attachment: fixed; }

.contenedorHeader {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 4rem;
  padding-bottom: 4rem;
  gap: 40rem; }

.menu {
  visibility: hidden; }
  @media (min-width: 767px) {
    .menu {
      visibility: visible;
      display: flex;
      gap: 4rem;
      max-height: 3rem; } }
  .menu a {
    font-size: 2rem;
    color: #ffffff;
    text-transform: uppercase; }
    .menu a:hover {
      color: #068F92;
      font-size: 2.5rem;
      transition: .3s color linear;
      transition: .5s font-size linear; }

.testimonial {
  text-align: left;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 2.5rem; }
  @media (min-width: 767px) {
    .testimonial {
      text-align: right;
      font-size: 4.5rem; } }

blockquote {
  margin: 0; }

.about {
  width: 100%;
  padding: 1rem;
  display: flex;
  flex-direction: column-reverse; }
  @media (min-width: 1023px) {
    .about {
      padding: 5rem;
      display: grid;
      grid-template-columns: 70% 30%;
      align-items: center;
      gap: 1rem; } }

.image {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; }
  .image img {
    opacity: 0;
    width: 95%;
    height: 95%; }
    @media (min-width: 767px) {
      .image img {
        width: 85%;
        height: 85%; } }
    @media (min-width: 1023px) {
      .image img {
        width: 100%;
        height: 95%; } }

.about-me {
  text-align: justify;
  overflow: hidden; }
  @media (min-width: 1023px) {
    .about-me {
      padding: 0 5rem 0 1.5rem; } }
  .about-me h3 {
    color: #ffffff;
    text-transform: uppercase;
    font-size: 2.8rem;
    padding-top: 2rem;
    padding-bottom: 4rem; }
    @media (min-width: 1023px) {
      .about-me h3 {
        font-size: 3rem;
        padding-top: 2rem;
        padding-bottom: 4rem;
        text-align: right; } }
  .about-me p {
    margin: 0;
    color: #ffffff; }
  .about-me a {
    background-color: #01386E;
    padding: 1.4rem 8rem;
    color: #0F0F0F;
    display: inline-block; }
    .about-me a:hover {
      background-color: #ffffff;
      color: #068F92;
      transition: 1s background-color linear; }

.boton {
  margin: 3rem 0;
  display: block; }
  @media (min-width: 767px) {
    .boton {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin: 0; } }
  .boton a {
    width: 100%;
    text-align: center; }
    @media (min-width: 767px) {
      .boton a {
        width: auto; } }
  .boton i {
    font-size: 2.2rem; }

.contenedorSkills {
  padding: 5rem 0; }
  .contenedorSkills ul {
    display: flex;
    flex-direction: column;
    gap: 10rem; }
    @media (min-width: 767px) {
      .contenedorSkills ul {
        display: grid;
        gap: 10rem;
        grid-template-columns: repeat(2, 1fr); } }
    @media (min-width: 1023px) {
      .contenedorSkills ul {
        display: grid;
        gap: 10rem;
        grid-template-columns: repeat(3, 1fr); } }

.skill {
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  gap: 2rem; }
  .skill i {
    font-size: 8rem; }
  .skill span {
    font-size: 3rem;
    user-select: none; }

h3 {
  color: #ffffff;
  text-transform: uppercase;
  padding-top: 3rem;
  padding-bottom: 8rem;
  text-align: start; }

.lista-trabajos {
  text-align: center; }
  @media (min-width: 1023px) {
    .lista-trabajos {
      display: grid;
      gap: 8rem;
      grid-template-columns: repeat(2, 1fr); } }

.trabajo {
  position: relative;
  padding-bottom: 5rem;
  margin-bottom: 5rem;
  border-bottom: 1px solid #ffffff; }
  @media (min-width: 1023px) {
    .trabajo {
      margin-bottom: 5rem;
      border-bottom: none; } }
  .trabajo img {
    width: 100%;
    height: 35rem; }

.busca-trabajo {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%; }
  @media (min-width: 1023px) {
    .busca-trabajo {
      position: absolute;
      top: 0;
      opacity: 0; }
      .busca-trabajo:hover {
        opacity: 1;
        transition: .5s opacity linear;
        background-color: rgba(15, 15, 15, 0.7); } }
  .busca-trabajo .nombre-trabajo {
    color: #068F92;
    text-transform: uppercase;
    font-family: Anurati, sans-serif;
    font-size: 3.6rem;
    margin-bottom: 2rem; }
  .busca-trabajo .descripcion-trabajo {
    color: #ffffff;
    font-size: 1.8rem; }

.contenedor-botones {
  display: flex;
  justify-content: space-evenly;
  margin-top: 3rem; }

.botones-trabajo {
  color: #ffffff;
  text-transform: uppercase;
  padding: 1rem 5rem; }
  .botones-trabajo:hover {
    border-radius: 1rem;
    background-color: rgba(15, 15, 15, 0.9);
    transition: .2s border-radius linear;
    transition: .3s background-color linear; }

.redes {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  color: #ffffff;
  align-items: center;
  gap: 5rem; }
  @media (min-width: 767px) {
    .redes {
      display: grid;
      gap: 5rem;
      grid-template-columns: repeat(2, 1fr); } }
  @media (min-width: 1023px) {
    .redes {
      display: flex;
      flex-direction: row;
      justify-content: space-between; } }
  .redes a {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    color: #ffffff;
    font-size: 2rem; }
    .redes a i {
      font-size: 4rem; }
      .redes a i:hover {
        color: #068F92;
        transition: .3s color linear; }

.footer {
  background-color: #0c0c0c;
  padding: 3rem 0;
  margin-top: 10rem;
  max-height: auto; }

.contenedorFooter {
  display: flex;
  justify-content: space-evenly;
  align-items: center; }
  .contenedorFooter p {
    color: #ffffff;
    font-size: 1.5rem; }
  .contenedorFooter .botones-trabajo {
    font-size: 1.4rem;
    padding-right: 0;
    text-align: center; }
    @media (min-width: 1023px) {
      .contenedorFooter .botones-trabajo {
        padding-right: 5rem; } }
